}
.page-fade-enter{
  opacity: 0;
}
.page-fade-enter-active{
  -webkit-transition: opacity 0.5s;
  transition: opacity 0.5s;
  -webkit-transition-delay: 0.5s;
          transition-delay: 0.5s;
}
.page-fade-leave-top{
  opacity: 0;
}
.page-fade-leave-active{
  -webkit-transition: opacity 0.4s;
  transition: opacity 0.4s;
}


